@import "../utils/mixins";
@import "../utils/variables";

.scrollbar {
  margin-top: 8px;
  margin-left: 12px;
  margin-right: 12px;
  
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background: #F5F5F5;
    box-shadow: inset 4px 0px 4px rgba(0, 0, 0, 0.04);
    border-radius: 1000px;
  }  
  &::-webkit-scrollbar-thumb {
    background: #391F75;
    outline: none;
    border-radius: 1000px;
    border: 2px solid rgb(235, 231, 231);
  }
}
.lists {
  max-width: 90%;
  @include mq(tablet) {
    max-width: 762px;
  }
  margin: auto;
  padding-right: 25px;
  overflow-y: scroll;
  max-height: 140px;
  &__item {
    display: flex;
    justify-content: space-between;
    color: #DDDBE3;
    padding-top: 14.5px;
    padding-bottom: 16px;
    border-bottom: 1px solid #DDDBE3;
    .btn-remove {
      cursor: pointer;
      span {
        &:first-child {
          margin-right: 8px;
        }
        &.description {
          display: none;
          @include mq(tablet) {
            display: inline;
          }
        }
      }
    }
  }
}