// -----------------------------------------------------------------------------
// This file contains all application-wide Sass mixins.
// -----------------------------------------------------------------------------

@mixin mq($width, $type: min) {
  @if map_has_key($breakpoints, $width) {
    $width: map_get($breakpoints, $width);
    @if ($type == min) {
      $width: $width + 1px;
    }
    @media only screen and (#{$type}-width: $width) {
      @content;
    }
  }
}
@mixin orientation($direction) {
  $orientation-landscape: "(orientation:landscape)";
  $orientation-portrait: "(orientation:portrait)";

  @if $direction == landscape {
    @media #{$orientation-landscape} {
      @content;
    }
  }
  @if $direction == portrait {
    @media #{$orientation-portrait} {
      @content;
    }
  }
}

// Typography mixins
@mixin heading1() {
  font: 600 35px/1.2 $f-heading;
  @include mq("tablet") {
    font-size: 48px;
  }
  @include mq("desktop") {
    font-size: 64px;
  }
}
@mixin heading2() {
  font: 600 32px/42px $f-heading;
  @include mq("tablet") {
    font-size: 36px;
    line-height: 50px;
  }
  @include mq("desktop") {
    font-size: 44px;
    line-height: 60px;
  }
}
@mixin heading3() {
  font: 500 20px/20.2px $f-text;
  @include mq("desktop") {
    font-size: 32px;
    line-height: 48px;
  }
}
@mixin heading4() {
  font: 600 20px/42px $f-heading;
  @include mq("tablet") {
    font-size: 36px;
    line-height: 50px;
  }
  @include mq("desktop") {
    font-size: 44px;
    line-height: 60px;
  }
}
@mixin heading5() {
}
@mixin heading6() {
}
@mixin bodyText($size: normal) {
  font: 400 15px/19.5px $f-text;
  color: $text_color;
  @include mq("tablet") {
    line-height: 22px;
  }
  @if $size == small {
    @include mq("tablet", "max") {
      font-size: 12px;
      line-height: 15.6px;
    }
  }
}
@mixin buttonText($size: normal) {
  @include bodyText($size);
  font-weight: 700;
}
@mixin pseudo-elements() {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@mixin coverImage() {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@function vw($px-vw, $base-vw: 1440px) {
  @return ($px-vw * 100vw) / $base-vw;
}

@mixin when-inside($classes...) {
  @each $class in $classes {
    #{$class} & {
      @content;
    }
  }
}
