#fourteenth-page {
  .btn {
    margin-top: 18px;
    border-color: #391F75;
    color: #391F75;
    font-weight: 600;
  }
  .emailed {
    display: flex;
    color: #67D2C4;
    align-items: center;
    margin: auto;
    justify-content: center;
    margin-top: 15px;
    font-size: 12px;
    img {
      margin-right: 2px;
    }
  }
  .appstore {
    max-width: 478px;
    margin: auto;
    padding-top: 27px;
    padding-bottom: 43.5px ;
    margin-top: 87.6px;
    background: #FFFFFF;
    box-shadow: 0px 4px 40px rgba(148, 120, 211, 0.25);
    border-radius: 8px;
    p {
      padding-left: 34.5px;
      padding-right: 34.5px;
      text-align: left;
      &:first-child {
        color: #7000FF;
        font-size: 32px;
        line-height: 48px;
      }
      &:nth-child(2) {
        margin-top: 12.5px;
        color: #391F75;
        font-size: 24px;
        line-height: 36px;
      }
    }
    .divider {
      margin-top: 30.8px;
      margin-bottom: 36px;
      border: 1px solid #F4F4F4;
    }
    &__items {
      img {
        &:first-child {
          margin-right: 17px;
        }
      }
    }
  }
}