// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------

// Header styles
header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 100;
  padding: 53px 0;
  @include mq(tablet-wide, max) {
    padding: 32px 0;
  }
  @include mq(phone, max) {
    padding: 10px;
  }
  .logo {
    cursor: pointer;
  }
  label {
    display: block;
    cursor: pointer;
    width: 50px;
    height: 50px;
    max-width: 50px;
    max-height: 50px;
  }
  
  #hamburger_icon {
    display: none;
  }
  path {
    fill: none;
    stroke: #391F75;
    stroke-width: 3;
    stroke-linecap: round;
    stroke-linejoin: round;
    --length: 24;
    --offset: -38;
    stroke-dasharray: var(--length) var(--total-length);
    stroke-dashoffset: var(--offset);
    transition: all 0.8s cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  
  circle {
    fill: #0003;
    opacity: 0;
  }
  
  label:hover circle {
    opacity: 1;
  }
  
  .cross input:checked+svg .line--1,
  .cross input:checked+svg .line--3 {
    --length: 22.627416998;
  }
  
  .cross input:checked+svg .line--2 {
    --length: 0;
  }
  
  .back input:checked+svg .line--1,
  .back input:checked+svg .line--3 {
    --length: 8.602325267;
  }
  
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .line--1,
    .line--3 {
      --total-length: 111.2281341553;
      --offset: -50.2281341553;
    }
    
    .line--2 {
      --total-length: 99;
    }
    
    input:checked+svg path {
      transform: translateX(30px);
    }
    
    input:checked+svg .line--1,
    input:checked+svg .line--3 {
      --offset: -16.9705627485;
    }
    
    input:checked+svg .line--2 {
      --offset: -20;
    }

    label {
      bottom: 0;
      left: 0;
    }
  }
  .navmenu {
    position: absolute;
    width: 200px;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: 99;
    transform: translateX(-100%); 
    transition: transform 1s ease-in-out;
    background: rgba(57,31,117,0.8);
    margin: 0;
    list-style-type: none;
    padding-top: 10%;
  }
  .navmenu__item {
    padding: 1rem;
  }
  .navmenu__item__link {
    cursor: pointer;
    color: white;
  }
}



