// -----------------------------------------------------------------------------
// This file contains all styles related to the common parts in all application
// -----------------------------------------------------------------------------
// Custom Color Classes
.footer-border::after {
  content: '';
  position: absolute;
  bottom: 159px;
  width: calc(100% - 128px);
  left: 50%;
  transform: translateX(-50%);
  border: 1px solid #ECD8FF;
  z-index: 0;
}
.bg-white {
  background-color: white;
}
body {
  background: linear-gradient(180deg, rgba(236, 216, 255, 0.63) 0%, rgba(196, 196, 196, 0) 53.78%);;
}
.container {
  width: 100%;
  position: relative;
}
.full-width {
  width: 100%;
}
.text-center {
  text-align: center;
}
.centered-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding-bottom: 30px;
}
.top-little {
  top: calc(50% - 44px);
  position: absolute;
  max-height: 78%;
}
.text-primary {
  color: $primary;
}
.bg-primary {
  background-color: $primary;
}
.text-secondary {
  color: $secondary;
}
.bg-secondary {
  background-color: $secondary;
}
.btn {
  border-radius: 8px;
  padding: 12px 17px 12px 15px;
  background: #7000FF;
  color: #fff;
  border: none;
  font-family: "Poppins";
  font-weight: 500;
  font-size: 16px;
  display: inline-flex;
  align-items: center;
  border: 2px solid #7000FF;
  min-width: 112px;
  min-height: 48px;
  justify-content: center;
  &.btn-disabled {
    cursor: not-allowed;
  }
}
.btn img {
  margin-left: 13px;
}
.btn-white {
  background: #FFFFFF;
  color: #7000FF;
  box-sizing: border-box;
}
.btn-flight {
  background: transparent;
  border: none;
}
.btn-remove {
  color: #B1ACBF;
}
.btn-flight span {
  font-size: 22px;
  margin-right: 8px;
}
.btn-add {
  color: #7000FF;
  background-color: transparent;
  border: none;
  font-size: 16px !important;
}
.px-64 {
  padding-left: 12px;
  padding-right: 12px;
  @include mq(tablet) {
    padding-left: 24px;
    padding-right: 24px;
  }
  @include mq(desktop) {
    padding-left: 64px;
    padding-right: 64px;
  }
  @include mq(desktop-wide) {
    padding-left: 64px;
    padding-right: 64px;
  }
}

.input-day, .input-month {
  width: 172px;
  @include mq(tablet) {
    width: 128px;
  }
}

.input-year {
  width: 172px;
}

.ml-tw-24 {
  @include mq(tablet) {
    margin-left: 12px;
  }
  @include mq(tablet-wide) {
    margin-left: 24px;
  }
}
.mt-tw-20 {
  
  @include mq(tablet, max) {
    margin-top: 20px;
  }
}
.mt-45 {
  margin-top: 45px;
}
.d-flex {
  display: flex;
}
.w-50 {
  width: 50%;
  position: relative;
}
.h-100 {
  min-height: 100vh;
}
.z-1 {
  z-index: 1;
}

#ui-datepicker-div {
	display: none;
	background-color: #fff;
	box-shadow: 0 0.125rem 0.5rem rgba(0,0,0,0.1);
	margin-top: 0.25rem;
	border-radius: 0.5rem;
	padding: 0.5rem;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.ui-datepicker-calendar {
	thead {
		th {
			padding: 0.25rem 0;
			text-align: center;
			font-size: 0.75rem;
			font-weight: 400;
			color: #78909C;
		}
	}
	tbody {
		td {
			width: 2.5rem;
			text-align: center;
			padding: 0;
      &.ui-datepicker-unselectable {
        color: #B1ACBF;
      }
      &.ui-datepicker-today {
        a {
          background-color: #F7446D;
          color: white;
        }
      }
			a {
				display: block;
				border-radius: 0.25rem;
				line-height: 2rem;
				transition: 0.3s all;
        color: #1D0060;
				font-size: 0.875rem;
				text-decoration: none;
        width: 30px;
        height: 30px;
        border-radius: 50px;
				&:hover {
					background-color: #E0F2F1;
				}
			}
      
			a.ui-state-active {
				background-color: #1D0060;
				color: white;
			}
		}
	}
}
.ui-datepicker-header {
	a.ui-corner-all {
		cursor: pointer;
		position: absolute;
		top: 0;
		width: 2rem;
		height: 2rem;
		margin: 0.5rem;
		border-radius: 0.25rem;
		transition: 0.3s all;
		&:hover {
			background-color: #ECEFF1;
		}
	}
	a.ui-datepicker-prev {
		left: 0;
		background: url("data:image/svg+xml;base64,");
		background-repeat: no-repeat;
		background-size: 0.5rem;
		background-position: 50%;
		transform: rotate(180deg);
	}
	a.ui-datepicker-next {
		right: 0;
		background: url('data:image/svg+xml;base64,');
		background-repeat: no-repeat;
		background-size: 10px;
		background-position: 50%;
	}
	a {
		>span {
			display: none;
		}
	}
}
.ui-datepicker-title {
	text-align: center;
	line-height: 2rem;
	margin-bottom: 0.25rem;
	font-size: 0.875rem;
	font-weight: 500;
	padding-bottom: 0.25rem;
  color: #1D0060;
  .ui-datepicker-year {
    display: none;
  }
}
.ui-datepicker-week-col {
	color: #78909C;
	font-weight: 400;
	font-size: 0.75rem;
}
.disabled {
  color: #DDDBE3;
  h2 {
    color: #DDDBE3;
  }
  input, select {
    color: #DDDBE3;
    border-color: #DDDBE3;
  }
}
.btn-trip {
  box-shadow: 0px 4px 40px #CA94FF;
  margin-top: 32px;
  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: left;
    span:first-child {
      font-weight: 500;
      font-size: 32px;
      line-height: 48px;
    }
  }
}
.travel-cards {
  display: flex;
  justify-content: space-between;
  margin-top: 66px;
  flex-wrap: wrap;
  .travel-card {
    width: 100%;
    position: relative;
    background: #FFFFFF;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    color: #391F75;
    margin-bottom: 20px;
    @include mq(tablet) {
      width: calc(50% - 12px);
      margin-left: 24px;
      &:first-child,
      &:nth-child(3) {
        margin-left: 0;
      }
    }
    @include mq(desktop) {
      width: calc(25% - 18px);
      &:nth-child(3) {
        margin-left: 24px;
      }
      margin-bottom: 0;
    }
    
    >span {
      display: block;
      text-align: left;
      padding-right: 32px;
      padding: 0 20px;
      &:first-child {
        font-weight: bold;
        padding-right: 50%;
        padding-top: 16px;
        padding-bottom: 8px;
      }
      &:nth-child(2) {
        padding-bottom: 12px;
      }
    }
    .divider {
      width: 100%;
      padding: 0;
      border: 1px solid #F4F4F4;
    }
    .travel-price {
      padding: 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      &__btn {
        width: 24px;
        height: 24px;
        font-weight: 600;
        font-size: 24px;
        line-height: 100%;
        text-align: center;
        color: #391F75;
        background: #F6EDFF;
        border-radius: 4px;
        border: none;
        padding: 0;
      }
      &__box {
        div {
          &:first-child {
            font-size: 12px;
          }
          &:last-child {
            font-size: 24px;
            font-weight: bold;
          }
        }
      }
    }
    /*Toggle Button*/
    .toggle-btn {
      position: absolute;
      top: 18px;
      right: 20px;
      .toggle-switch {
        display: none;
        position: relative;
        + label {
          display: block;
          cursor: pointer;
          text-align: left !important; /* To keep the switch from getting misaligned */
          span.sw {
            display:inline-block;
            width: 44px;
            height: 24px;
            background: rgba(177, 172, 191, 0.1);
            box-shadow: inset 0px 4px 12px rgba(0, 0, 0, 0.12);
            border-radius: 54px;
            transition: all ease 300ms;
            &::before {
              content:'';
              position:absolute;
              background-color : #fff;
              margin-top: 3.8px;
              margin-left: 3.8px;
              height: 16px;
              width: 16px;
              border-radius: 15px;
              transition: all ease 300ms;
              box-shadow: 0px 2px 8px rgba(0,0,0,0.3);
            }
          }
        }
        &:checked+ label span.sw{
          background: #67D2C4;
          box-shadow: inset 0px 4px 12px rgba(0, 0, 0, 0.12);
          &::before {
            margin-left: 24.3px;
            background-color : #fff;
          }
        }
      }
    }
    &.disable {
      .travel-price {
        color: rgba(177, 172, 191, 0.4);
        &__btn {
          color: white;
          background: rgba(177, 172, 191, 0.4);
          cursor: not-allowed;
        }
      }
    }
  }
  &.clicked {
    .travel-card span:nth-child(2) {
      display: none;
    }
  }
}

.travel-policy {
  margin-top: 78px;
  display: flex;
  justify-content: space-between;
  color: #1D0060;
  text-align: left;
  flex-wrap: wrap;
  @include mq(desktop) {
    flex-wrap: nowrap;
  }
  &__des{
    width: 100%;
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 36px;
    @include mq(desktop) {
      width: 25%;

    }
  }
  &__methods {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include mq(desktop) {
      margin-left: 24px;
      width: 50%;
    }
    &__item {
      width: 100%;
      margin-bottom: 40px;
      @include mq(phone) {
        width: calc(50% - 20px);
        margin-left: 40px;
        &:first-child,
        &:nth-child(3) {
          margin-left: 0;
        }
      }
      @include mq(tablet) {
        margin-left: 40px;
        width: calc(25% - 30px);
        &:nth-child(3) {
          margin-left: 40px;
        }
        margin-bottom: 0;
      }
    }
  }
  &__btn {
    width: 100%;
    margin-left: 24px;
    text-align: right;
    @include mq(desktop) {
      width: 25%;
    }
  }
  &.disable {
    color: #B5AEC7;
  }
}
.travel-cards.clicked + .travel-policy {
  margin-top: 166px;
}
.travel-methods {
  display: none;
  max-width: 764px;
  margin:0 auto;
  margin-top: 51px;
  flex-wrap: wrap;
  &__item {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 20px;
    @include mq(phone) {
      margin-left: 84px;
      width: calc(50% - 42px);
      &:first-child,
      &:nth-child(3) {
        margin-left: 0;
      }
    }
    @include mq(tablet-wide) {
      width: calc(25% - 69px);
      &:nth-child(3) {
        margin-left: 84px;
      }
      margin-bottom: 0;
    }
    span {
      &:first-child {
        width: 56px;
        height: 56px;
        background: #DDDBE3;
        border-radius: 50%;
      }
      &:last-child {
        margin-top: 28px;
        color: #1D0060;
        text-align: left;
      }
    }
  }
}
form {
  @include mq('tablet', 'max') {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}
.form-group {
  display: inline-block;
  position: relative;
  small {
    position: absolute;
    bottom: -20px;
    left: 0;
    color: red;
  }
}
.btn_done {
  transform: rotate(45deg);
  color: #B1ACBF;
  margin-left: 10px;
  margin-right: 0;
}

.react-date-picker {
  &__wrapper {
    & > button {
      display: none !important;
    }
    border: none !important;
    .react-date-picker__inputGroup {
      text-align: left;
      .react-date-picker__inputGroup__input:invalid {
        background: transparent !important;
      }
    }
  }
}
.react-date-picker__calendar {
  .react-calendar {
    padding: 22px 32px 40px 32px;
    width: 340px;
    border: none;
    margin-top: 10px;
    button {
      font-size: 15px;
      color: #1D0060;
      &.react-calendar__month-view__days__day--neighboringMonth {
        color: #B1ACBF;
      }
    }
    &__navigation {
      &__prev2-button, &__next2-button {
        display: none;
      }
      &__arrow {
        font-size: 22px !important;
      }
      &__label {
        font-size: 16px !important;
        font-weight: 600 !important;
      }
    }
    &__month-view__weekdays,
    &__month-view__days {
      color: #1D0060;
      font-size: 15px;
      font-weight: 500;
      abbr {
        text-decoration: none;
      }
      .react-calendar__tile {
        border-radius: 50px;
      }
      .react-calendar__tile--active {
        background: #1D0060;
        color: white;
      }
    }
  }
}

.prev {
  position: fixed;
  bottom: 80px;
  left: 64px;
  color: #1D0060;
  cursor: pointer;
  background: transparent;
  border: none;
  display: flex;
  align-items: center;
  span {
    margin-left: 30px;
    font-weight: 600;
    font-size: 16px;
  }
}
button[disabled] {
  cursor: not-allowed;
}
.phone-num {
  display: block;
  width: 290px;
  margin: auto;
  .react-tel-input {
    .selected-flag {
      width: 60px;
      padding-left: 16px;
      .arrow {
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-top: 6px solid #1D0060;
        &.up {
          border-bottom: 6px solid #1D0060;
        }
      }
    }
    .form-control {
      height: 48px;
      padding-left: 68.5px;
    }
    .flag-dropdown {
      height: 48px;
      .country-list {
        text-align: left;
        &::-webkit-scrollbar {
          width: 8px;
        }  
        &::-webkit-scrollbar-track {
          background: #F5F5F5;
          box-shadow: inset 4px 0px 4px rgba(0, 0, 0, 0.04);
          border-radius: 1000px;
        }  
        &::-webkit-scrollbar-thumb {
          background: #391F75;
          outline: none;
          border-radius: 1000px;
          border: 2px solid rgb(235, 231, 231);
        }
        .country {
          &:hover {
            background: rgba(177, 172, 191, 0.2);
          }
          .country-name {
            color: #391F75;
          }
          .dial-code {
            color: #B1ACBF;
          }
        }
      }
    }
  }
}