// -----------------------------------------------------------------------------
// This file contains all application-wide Sass variables.
// -----------------------------------------------------------------------------

//Colors
$primary: #391F75;
$secondary: #B5AEC7;
$assist: #B5AEC7;
$accent: #008481;
$text_color: #fff;
$heading_color: #000;
$light-yellow: #fac66f;
$light-purple: #bfa8e5;
$light-accent: #91c3c3;
$bg_color: #fcf7f0;
$bg-button-color: #7000FF;

//Media query breakpoints
$breakpoints: (
  "phone-xsmall": 320px,
  "phone-small": 375px,
  "phone": 425px,
  "phone-wide": 480px,
  "phablet": 560px,
  "tablet-small": 640px,
  "tablet": 768px,
  "tablet-wide": 992px,
  "desktop-small": 1024px,
  "desktop": 1200px,
  "desktop-wide": 1439px,
  "desktop-ultra": 1599px,
);

// Font name
$f-heading: "Poppins";
$f-text: "Poppins";
