#nineth-page {
  .form-groups {
    margin-top: 73px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .flight-box {
      display: inline-flex;
      &__wrapper {
        position: relative;
        &__before {
          position: absolute;
          width: 100%;
          top: -30px;
          left: 0;
          text-align: left;
          padding-left: 12px;
          color: #B1ACBF;
          font-size: 12px;
          display: flex;
          align-items: center;
          visibility: hidden;
        }
        .focus + .flight-box__wrapper__before {
          visibility: visible;
        }
      }
      .btn-flight {
        min-width: 170px;
        justify-content: start;
      }
    }
  }
  .centered-box {
    & > .button-group {
      margin-top: 48px;
      .btn:last-child {
        margin-left: 29px;
      }
    }
  }
}