#fourth-page {
  p {
    margin-top: 24px;
  }
  .button-group {
    margin-top: 109px; 
  }
  .btn:last-child {
    margin-left: 29px;
  }
  .my-alert {
    max-width: 50%;
    margin: auto;
    color: grey;
    font-size: 18px;
    margin-top: 20px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    text-align: left;
    background: #FFFFFF;
    box-shadow: 0px 4px 40px rgba(148, 120, 211, 0.25);
    border-radius: 8px;
    line-height: 30px;
    padding: 12px;
  }
}