.card-input,
.med-input,
.sm-input {
  position: relative;
  label {
    position: absolute;
    top: -20px;
    color: #B1ACBF;
    font-size: 12px;
  }
  input  {
    max-width: 318px;
    width: 318px;
  }
}
.card-input {
  width: 318px;
  margin: auto;
  &:first-child {
    margin-top: 46px;
  }
}
.card-number {
  margin-top: 38px;
  .credit-cards {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 13px;
    img {
      margin-left: 8px;
      cursor: pointer;
      &.disable {
        filter: grayscale(100%);
      }
    }
  }
}
.med-input {
  margin: auto;
  margin-top: 38px;
  margin-bottom: 45.6px;
  max-width: 318px;
  display: flex;
  width: 318px;
  justify-content: space-between;
  .input-month {
    width: 84px;
  }
  .input-year {
    width: 105px;
    margin-right: 10px;
  }
  .sm-input {
    width: 89px;
    input {
      width: 89px;
    }
  }
}