.dropdown {
  position: relative;
  display: inline-block;
  &__option {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    img {
      visibility: hidden;
    }
    &.show {
      img {
        visibility: visible;
      }
    }
  }
  &__list {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    text-align: left;
    height: 180px;
    overflow-y: auto;
    z-index: 100;
    background-color: #fff;
    margin-top: 10px;
    width: 100%;
    &.show {
      display: block;
    }
    &__item {
      padding: 6px 12px;
      border-bottom: 1px solid #B1ACBF;
      color: B1ACBF;
      cursor: pointer;
      &.disable {
        cursor: not-allowed;
      }
      &:hover {
        color: white;
        background-color: #1D0060;;
      }
    }
  }  
}